.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: #fff;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  color: #fff;
  outline: none;
  transition: 0.5s;
}

body {
  background-color: #e1e388;
}

.box {
  background-color: #d4d37f;
  border: 2px solid #aaa719;
  margin-bottom: 20px;
  padding: 10px;
  width: 50%;
}

@media (max-width: 800px) {
  .box {
    width: 100%;
  }
}

.box:last-child {
  margin-bottom: 0;
}

button.btn {
  color: #fff;
  cursor: pointer;
  font-size: 0.65em;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
  padding: 10px;
}

@media (max-width: 800px) {
  button.btn {
    margin-left: 0;
    margin-right: 0;
  }
}

.field {
  align-items: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  font-size: 1.3em;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  width: 50%;
}

@media (max-width: 400px) {
  .field {
    width: 75%;
  }
}

.text {
  text-align: center;
}

.btn.red {
  background-color: #dda66d;
  border: 2px solid #b6821a;
  float: right;
}

@media (max-width: 800px) {
  .btn.red {
    width: 100%;
  }
}

.btn.new {
  background-color: #dd567e;
  border: 2px solid #c93964;
  margin-bottom: 10px;
}

textarea {
  background-color: #fafafa;
  border: 1.5px solid #c0c0c0;
  border-radius: 5px;
  color: #000;
  display: block;
  font-size: 0.7em;
  margin: 5px;
  margin-left: auto;
  margin-right: auto;
  outline: none;
  padding: 10px;
  resize: none;
  width: 90%;
}

.btn.success {
  background-color: #4fc966;
  border: 2px solid #2aa24f;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.btn.light {
  background-color: #4eafc9;
  border: 2px solid #2a89a2;
  float: left;
}

@media (max-width: 800px) {
  .btn.light {
    width: 100%;
  }
}

textarea:focus {
  border: 1.5px solid #6d79cd;
}

.btn.light:hover {
  background-color: #2a89a2;
  border-color: #1d6f85;
}

.btn.red:hover {
  background-color: #b6821a;
  border-color: #946912;
}

.btn.success:hover {
  background-color: #2aa24f;
  border: 2px solid #1e853e;
}

.btn.new:hover {
  background-color: #c93964;
  border: 2px solid #ac254d;
}
